<script>
import Completed from "./Completed";
// import VueGoogleAutocomplete from "vue-google-autocomplete";
export default {
	// components: { VueGoogleAutocomplete },
	data() {
		return {
			// address: "",
			loading: true,
			form: {
				street: "",
				street2: "",
				city: "",
				state: "",
				zip: ""
				// primary_address: false
			}
		};
	},
	components: {
		Completed
	},
	created() {
		sleep(2000).then(() => {
			this.loading = false;
		});
	},
	mounted() {
		this.scrollToTop;
		this.$refs.address.focus();
	},
	methods: {
		scrollToTop() {
			window.scrollTo(0, 0);
		},

		/**
		 * When the location found
		 * @param {Object} addressData Data of the found location
		 * @param {Object} placeResultData PlaceResult object
		 * @param {String} id Input container ID
		 */
		getAddressData: function(addressData, placeResultData, id) {
			this.address = addressData;
		},
		collectBilling: function(form) {
			this.loading = true;
			let formData = JSON.stringify(this.form);
			// this.$store.commit("updateBilling", formData);
			localStorage.setItem("street_address", this.form.street);
			localStorage.setItem("address2", this.form.street2);
			localStorage.setItem("city", this.form.city);
			localStorage.setItem("state", this.form.state);
			localStorage.setItem("zip_code", this.form.zip);

			sleep(2000).then(() => {
				this.$router.push("payment");
				this.loading = false;
			});
		}
	}
};

const sleep = milliseconds => {
	return new Promise(resolve => setTimeout(resolve, milliseconds));
};
</script>

