					<div class="phoneConfirmationSmsIcon"></div>

					<h1 class="headerText adjustHeader">Enter your code</h1>

					<p class="slimTopP">
						We sent a 6-digit code to
						<span>{{ loginUser }}</span>.
					</p>

					<div id="notifications" class="notifications"></div>

					<form class="proceed maskable" autocomplete="off" name="verifyOtp" novalidate>
						<div class="textInput" id="security_codediv">
							<div class="fieldWrapper">
								<label for="security_code" class="fieldLabel">Confirmation Code</label>
								<input
									v-model="code"
									id="security_code"
									name="security_code"
									type="password"
									class="hasHelp validate validateEmpty"
									required="required"
									value
									autocomplete="off"
									placeholder="Confirmation Code"
									maxlength="6"
								/>
							</div>

							<!-- <div class="errorMessage" id="security-divErrorMessage">
								<p class="emptyError hide">Type your code to continue.</p>
								<p class="invalidError hide">Check your code and try again.</p>
							</div>-->

							<div class="resend">
								<a
									v-if="resentBtn == false || sent == false || invalidCode == true"
									@click.prevent="resendCode()"
									pa-marked="1"
								>Resend</a>
								<span v-if="sent == true" class="sent">Sent!</span>
								<span v-if="invalidCode == true" class="failed">Invalid code!</span>
							</div>
						</div>

						<!-- <div class="actionsSpaced">
							<button
								@click.prevent="collectBilling()"
								class="button actionContinue scTrack:confirm"
								type="submit"
								pa-marked="1"
