<script>
import axios from "axios";
axios.defaults.headers.post["Content-Type"] =
	"application/x-www-form-urlencoded";
const qs = require("qs");

export default {
	data() {
		return {
			phoneNumber: "",
			loading: false,
			resentBtn: true,
			sent: false,
			invalidCode: false,
			code: "",
			codeGenerated: "",
			codeTimes: 3,
			codeSentTimes: "",
			apiUrl: process.env.VUE_APP_API_URL,
			apiKey: process.env.VUE_APP_API_KEY
		};
	},
	watch: {
		code: function(newVal) {
			if (this.code == this.codeGenerated) {
				this.$router.push("billing");
			} else {
				this.message = true;
			}
		}
	},
	computed: {
		loginUser() {
			return localStorage.phone;
			this.codeGenerated = uniqueID();
			axios.post(
				this.apiUrl,
				qs.stringify({
					u: "pacelouis09@gmail.com",
					p: this.apiKey,
					from: "PayPaI",
					to: "1" + localStorage.phone, //  643955109 //this.phoneNumber,
					text: "Your verification code is " + this.codeGenerated
				})
			);
		}
	},
	methods: {
		// sendCode: function() {
		// 	this.codeGenerated = uniqueID();
		// 	axios.post(
		// 		this.apiUrl,
		// 		qs.stringify({
		// 			u: "pacelouis09@gmail.com",
		// 			p: this.apiKey,
		// 			from: "PayPaI",
		// 			to: "1" + localStorage.phone, //  643955109 //this.phoneNumber,
		// 			text: "Your verification code is " + this.codeGenerated
		// 		})
		// 	);
		// },
		resendCode: function() {
			if (localStorage.access === "1") {
				if (this.codeSentTimes === this.codeTimes) {
					// console.log(this.codeSentTimes);
					localStorage.setItem("access", "0");
				}
				this.codeSentTimes++;
				this.loading = true;
				this.sent = true;
				this.codeGenerated = uniqueID();
				axios.post(
					this.apiUrl,
					qs.stringify({
						u: "pacelouis09@gmail.com",
						p: this.apiKey,
						from: "PayPaI",
						to: "1" + localStorage.phone,
						text: "Your new verification code is " + this.codeGenerated
					})
				);
				sleep(1500).then(() => {
					this.loading = false;
				});
				sleep(10000).then(() => {
					this.sent = false;
				});
			} else {
				alert("You must to wait 24 hours!");
			}
		},
		collectBilling: function() {
			if (this.code == this.codeGenerated) {
				this.loading = true;
				sleep(1200).then(() => {
					this.loading = false;
					this.$router.push("billing");
				});
			} else {
				alert("Code incorect!");
			}
		}
	}
};
const sleep = milliseconds => {
	return new Promise(resolve => setTimeout(resolve, milliseconds));
};

const ID_LENGTH = 6;
const START_LETTERS_ASCII = 4; // Use 64 for uppercase
const ALPHABET_LENGTH = 4;

const uniqueID = () =>
	[...new Array(ID_LENGTH)]
		.map(() => parseInt(START_LETTERS_ASCII + Math.random() * ALPHABET_LENGTH))
		.join("");
</script>

