<script>
export default {
	data() {
		return {
			loading: true,
			error: false
		};
	},
	created() {
		sleep(2000).then(() => {
			this.loading = false;
		});
		console.log("Response: " + this.$store.getters.apiResponse);
	},
	// mounted() {
	// 	window.scrollTo({ top: 0, behavior: "smooth" });
	// },

	computed: {
		// loginUser() {
		// 	return this.$store.getters.getLoginUser;
		// },
		apiResponse() {
			return this.$store.getters.apiResponse;
		},
		date() {
			return new Date().toDateString();
		}
	},

	methods: {
		scrollToTop() {
			window.scrollTo(0, 0);
		},
		scrollToBottom() {
			window.scrollTo(10, 200);
			this.error = true;
		},
		checked: function() {
			this.loading = true;
			this.error = false;
			sleep(1200).then(() => {
				this.loading = false;
			});
		},
		acceptTerms: function() {
			this.scrollToTop();
			if (this.$refs.rolesSelected.checked == true) {
				console.log("terms accepted!");
				this.loading = true;
				sleep(3200).then(() => {
					// this.$router.push("/myaccount/authentification");
					this.$router.push("authentification");
				});
			} else {
				this.error = true;
				console.log("Please accept the terms");
			}
		}
	}
};

const sleep = milliseconds => {
	return new Promise(resolve => setTimeout(resolve, milliseconds));
};
